.about-container {
    background: -webkit-gradient( linear, left top, left bottom, from(#fcd337), to(transparent));
    background: -webkit-linear-gradient( #fcd337, transparent);
    background: -o-linear-gradient( #fcd337, transparent);
    background: linear-gradient( #fcd337, transparent);
    height: 50vh;
}

h1 {
    padding-top: 2em;
    text-align: center;
    font-family: 'Montserrat', sans-serif;
    font-weight: 100;
}

p{
    padding: 1em;
    font-weight: 100;
}


