.navigation{
    width: 100vw;
    height: 30vh;
    position: absolute;
    z-index: 1;
    top: 70%;
    left: 50%;
    -webkit-transform: translateX(-50%);
    -ms-transform: translateX(-50%);
        transform: translateX(-50%);
    text-align: center;
    background: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(#fcd337), to(#fcd337));
    background: -webkit-linear-gradient(transparent, #fcd337, #fcd337);
    background: -o-linear-gradient(transparent, #fcd337, #fcd337);
    background: linear-gradient(transparent, #fcd337, #fcd337);
}

.nav-item-container {
    margin-top: 150px;
    width: 100%;
}

a {
    margin-top: 290px;
    text-decoration: none;
    color: white;
    text-transform: uppercase;
    padding: 0.3em;
    letter-spacing: 2px;
}



